@if $use-core-base == true {

    * {
        @include box-sizing("border-box");
    }

    body {
        margin: 0;
        padding: 0;
        position: relative;
    }

    span,
    div,
    b,
    strong {
        &[title] {
            cursor: help;
            &:hover {
                text-decoration: underline;
            }
        }
    }

    a[title],
    a strong[title],
    a span[title],
    a div[title],
    a b[title] {
        cursor: pointer;
    }

    p,
    blockquote,
    dl, dt, dd,
    figure,
    figcaption,
    div,
    hr,
    article,
    nav,
    aside,
    main,
    section,
    header,
    footer,
    h1, h2, h3, h4, h5, h6
    hgroup,
    address {
        margin:0;
        padding:0;
    }

    ul {
        text-align: left;
    }

    nav {
        ul {
            list-style-type: none;
            padding: 0;
            li {
                margin-left:0;
            }
        }
    }
}
